.head {
    height: 40px;
    background-color: rgb(255, 133, 42);
}

.head>span {
    color: white;
    font-family: '';
    margin-left: 10px;
    line-height: 35px;
    font-size: 20px;
}

.head .sear {
    background-color: rgb(255, 78, 34);
    height: 25px;
    width: 85%;
    color: #fff;
    margin-top: 5px;
    border-radius: 8px;
    border-color: rgb(255, 78, 34);
}

input::-webkit-input-placeholder {
    /* placeholder颜色  */
    color: #fff;
}

.detail .boxs {
    width: 45%;
    display: inline-block;
    background-color: #fff;
    margin-left: 15px;
}

.detail img {
    width: 100%;
}

.detail p {
    margin-left: 15px;
}

.detail p:first-child {
    font-weight: 700;
}

.detail p:nth-child(2n+1) {
    color: rgb(255, 78, 34);
}

.detail p:last-child {
    color: #ccc;
}